.howtowork{
  width: 100%;
  float: left;
  position: relative;
  background-color: #FFF8DC;
  padding: 45px 0 185px;
  box-sizing: border-box;
  .title{
    margin-bottom: 60px;
    h2 span{
      display: inline-block;
    }
  }
}


.howtowork__video{
  width: 52%;
  float: left;
  position: relative;
  background-repeat: no-repeat;
  background-image: url("../img/ipad.png");
  height: 325px;
  // background-size: contain;
  iframe {
      width: 386px;
      height: 290px;
      position: absolute;
      left: 46px;
      top: 15px;
  }
}

.howtowork__list{
  width: 42%;
  float: left;
  position: relative;
  padding-left: 55px;
  box-sizing: border-box;
}

.howtowork__list_item{
  width: 100%;
  float: left;
  position: relative;
  text-align: left;
  .number{
    float: left;
    position: relative;
    color: #4e65a6;
    font-family: $b;
    font-size: 36px;
    // font-weight: 700;
    position: relative;
    top: 15px;
    margin-right: 20px;
  }
  p{
    width: calc(100% - 55px);
    float: left;
    color: #555;
    font-family: $r;
    font-size: 17px;
    font-weight: 400;
    line-height: 16px;
  }
}
